@import 'variables';
@import 'mixins';

@import 'custom-bootstrap/bootstrap';
@import 'base-styles';

@import 'header';
@import 'letter-grades';

#restaurant-inspections-branding {
    font-size: 0;
    margin-bottom: 36px;
    margin-top: 36px;

    h4 {
        display: block;
        float: left;
        font-family: 'Alfa Slab One', cursive;
        font-size: 2rem;
        font-weight: normal;
        line-height: 2.4rem;
        margin: 0;
    }

    .score-swatches {
        display: block;
        float: left;
        margin-left: 3px;
        margin-top: 0.65rem;

        .score-swatch {
            border-radius: 6px;
            display: inline-block;
            height: 12px;
            margin-left: 5px;
            width: 12px;
        }
    }
}

#inspection-detail {
    background-color: #FFFFFF;
    border-bottom: 0 none;
    box-shadow: 2px 2px 5px rgba(51,51,51,0.25);
    color: #333333;
    font-family: $font-family-sans;
    font-size: 20px;
    margin-bottom: 35px;
    padding: 4rem;
    padding-bottom: 0;
    position: relative;

    &::before {
        background-color: $background-color;
        content: '';
        display: block;
        height: 40px;
        left: -20px;
        position: absolute;
        top: -20px;
        width: 40px;

        -webkit-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
             -o-transform: rotate(45deg);
                transform: rotate(45deg);
    }

    .top-line-information {
        left: 0;
        min-height: 150px;
        position: relative;
        top: 0;

        .details {
            left: 156px;
            position: relative;
            width: calc(100% - 156px);

            h2 {
                font-family: 'Alfa Slab One', cursive;
                font-size: 4.236rem;
                font-weight: normal;
                letter-spacing: 1.5px;
                line-height: 5.85rem;
                margin: 0;
            }

            p {
                font-size: 1.618rem;
                line-height: 28px;
                margin: 0;
            }
        }
    }

    .divider {
        border-bottom: 3px solid $background-color;
        margin-bottom: 2.4rem;
        padding-bottom: 2.4rem;
    }

    .more-overall-information {
        display: none;
        h6 {
            font-family: 'Alfa Slab One', cursive;
            font-size: 2.618rem;
        }
    }

    .end-line {
        border: 0 none;
        height: 5px;
        margin: 4rem -4rem 0;
        width: calc(100% + 8rem);
    }

    #dropdown {
        margin-top: 10px;

        p {
            display: inline;
            margin-bottom: 0;
        }

        #inspectionselect {
            font-family: $font-family-sans;
            font-size: 15px;
            width: 169px;
        }
    }

    #inspectiondetails {
        h3 {
            margin-bottom: 0;
        }

        p {
            font-size: 16px;
        }

        table {
            border-collapse: collapse;
            font-size: 16px;
            line-height: 24px;
            margin: 0 10px 0 10px;

            tr:nth-child(1) {
                border-bottom: 1px solid #d7d7d7;
                padding: 12px 12px;
            }

            th {
                padding: 0 12px;
            }

            td {
                padding: 15px 15px;
            }

            td:first-child,
            th:first-child {
                padding: 0 0;
            }
        }
    }
}

.all-inspections {
    font-family: "Gotham A","Gotham B",Arial,sans-serif;
    margin-bottom: 25px;

    .ribbon {
        // align-items: center;
        // background: #FFFFFF;
        // display: flex;
        // flex-direction: row;
        // flex-wrap: nowrap;
        font-size: 0;
        height: 80px;
        // justify-content: flex-start;
        overflow: hidden;
        position: relative;
        z-index: 1;

        &::before {
            background: #5B5B5B;
            bottom: 0;
            content: '';
            height: 80px;
            left: 0;
            position: absolute;
            top: 0;
            width: 1.7rem;
            z-index: 34;
        }

        .ribbon-heading {
            color: #FFFFFF;
            cursor: default;
            float: left;
            font-size: 1.7rem;
            font-weight: normal;
            height: 80px;
            left: 0;
            line-height: 25px;
            margin-right: -1px;
            max-width: 100%;
            min-width: 160px !important;
            opacity: 1;
            padding-bottom: 1.5rem;
            padding-left: 1.8rem;
            padding-right: 1.8rem;
            padding-top: 1.5rem;
            position: absolute;
            text-align: left;
            text-transform: uppercase;
            top: 0;
            width: 160px;
            z-index: 33;

            transition: padding 200ms, max-width 300ms, opacity 250ms ease-out 400ms, color 300ms 900ms;

            &::before {
                background: #5B5B5B;
                border-right: 1px solid rgba(26,26,26,0.6);
                bottom: 0;
                box-shadow: 3px 0px 14px -1px rgba(0,0,0,0.7);
                content: '';
                display: block;
                left: 0;
                position: absolute;
                right: 0;
                top: 0;
                transition: background 150ms;
                z-index: -1;

                -webkit-transform: skew(-15deg);
                   -moz-transform: skew(-15deg);
                     -o-transform: skew(-15deg);
            }
        }

        .item-list {
            left: 150px;
            overflow: scroll;
            padding-left: 10px;
            position: absolute;
            transition: box-shadow 250ms;
            top: 0;
            width: calc(100% - 150px);

            &.end-shadow {
                box-shadow: inset -8px 0 12px -7px #808080;
            }

            .ribbon-track {
                height: 80px;
                position: relative;
                white-space: nowrap;
                // width: 150%;
            }
        }

        .ribbon-item {
            // align-self: stretch;
            color: #0f0f0f;
            cursor: pointer;
            display: inline-block;
            // float: left;
            font-size: 1.5rem;
            // font-weight: bold;
            height: 80px;
            min-width: 90px;
            // max-width: 100%;
            opacity: 1;
            padding-bottom: 2.25rem;
            padding-left: 1.75rem;
            padding-right: 1.75rem;
            padding-top: 2.25rem;
            position: relative;
            text-align: center;
            transition: padding 200ms, max-width 300ms, min-width 300ms, opacity 250ms ease-out 400ms, color 300ms 900ms;

            &::before {
                background: #f7f7f7;
                border-bottom: 1px solid rgba(51,51,51,0.25);
                border-right: 1px solid rgba(51,51,51,0.25);
                border-top: 1px solid rgba(51,51,51,0.25);
                bottom: 0;
                box-shadow: inset 7px 0px 6px -6px rgba(51,51,51,0.19);
                content: '';
                display: block;
                left: 0;
                position: absolute;
                right: 0;
                top: 0;
                transition: background 150ms;
                z-index: -1;

                -webkit-transform: skew(-15deg);
                   -moz-transform: skew(-15deg);
                     -o-transform: skew(-15deg);
            }

            &:hover::before {
                background-color: darken(#f7f7f7, 10%);
            }

            span {
                display: block;
            }

            &.disabled {
                min-width: 0;
                overflow: hidden;
                padding: 2.25rem 0;
                width: 0;

                span {
                    opacity: 0;
                    transition: opacity 100ms;
                }
            }
        }
    }
}

@media screen and (max-width: 400px) {
    #inspection-detail {
        font-size: 15px;
    }

    .grade-box {
        width: 100px;

        .grade {
            font-size: 72px;
            line-height: 70px;
        }
        .score {
            font-size: 15px;
        }
    }

    h2 {
        font-size: 24px;
    }
    #inspection {
        font-size: 15px;
        width: 150px;
    }
    table {
        font-size: 12px;
    }
}
